import React from 'react';
import './modal.css';

const Modal = (props) => {
  const {
    className,
    children,
    setVisible,
    visible,
    ...rest
  } = props;

  const callSetVisible = () => {
    setVisible();
    console.log('clicked');
  };

  return (
    visible ?
    <div className="modal" {...rest} onClick={setVisible ? callSetVisible : null}>
      <div className="modal_content">
        {children}
      </div>
    </div>
    : null
  );
};

export default Modal;
